html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 0;
  /* Margin bottom by footer height */
}

h1 {
  font-size: 40px !important;
  font-weight: bold !important;
}

.spacing-top {
  margin-top: 50px !important;
}

@media (min-width: 968px) {
  .m-top-100 {
    margin-top: 100px;
  }
}

.nav-link {
  color: white !important;
  font-size: 18px !important;
}

.error {
  background-color: red;
}

.alerta {
  display: block;
  text-align: center;
  width: 100%;
  margin: 2.5rem auto;
  font-weight: bold;
  color: white;
  padding: 1rem 0.5rem;
}

.formulario {
  max-width: 90%;
  margin: 5rem auto !important;
  background-color: white !important;
}
@media (min-width: 968px) {
  .formulario {
    max-width: 50%;
  }
}

.my-footer {
  background-color: #2c2c2c;
  height: auto;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-top: 50px;
}
.my-footer p, .my-footer a {
  color: white;
  font-size: 16px;
  margin-bottom: 5px;
}
@media (min-width: 968px) {
  .my-footer {
    display: grid;
    grid-template-columns: 1fr, 1fr;
    grid-template-rows: 1fr 1fr;
    padding: 1rem;
  }
  .my-footer p {
    grid-row: 1/2;
  }
  .my-footer a {
    grid-column: 1/3;
  }
}

.darkText {
  color: #b1b1b1;
}

.darkBackground {
  background-color: #333333;
}

.dark-mode-moon {
  max-width: 1.5rem;
  max-height: 1.5rem;
  filter: invert(1);
}

.imagen-logo source, .imagen-logo img {
  max-width: 350px;
  justify-content: center;
  align-items: center;
}
@media (min-width: 968px) {
  .imagen-logo source, .imagen-logo img {
    max-width: 100%;
  }
}

.map iframe {
  display: flex !important;
  justify-content: center !important;
  margin: 0 auto !important;
}

.formulario {
  width: 95%;
  background-color: #d1e6e6;
  margin: 0 auto;
  padding: 10px;
  border-radius: 10px;
  filter: drop-shadow(0 10px 5px rgba(0, 0, 0, 0.2));
}
.formulario fieldset {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.formulario fieldset label {
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 5px;
}
.formulario fieldset input {
  display: block;
}
.formulario fieldset input:last-of-type {
  margin-bottom: 10px;
}
.formulario .line {
  background: rgba(0, 0, 0, 0.7);
  height: 1px;
  width: 90%;
  margin: 10px auto;
}
.formulario .radio {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.formulario .radio label {
  margin: 0 5px;
}
.formulario .radio input label {
  margin: 0 5px;
}